html, body, #root, .App, .FileDrop, .Workspace, .Document {
  height: 100%;
}

button {
    background: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    display: inline;
    font-family: inherit;
    outline: none;
    padding: 0;
}

body {
  background: #333;
  color: #aaa;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

input {
  background: transparent;
  border: none;
  border-bottom: solid 1px;
  color: inherit;
  font-family: inherit;
}


